import style from "./About.module.scss";
import { useState } from "react";

export const About = () => {
  const [isExpanded, setIsExpanded] = useState(false);

  const toggleExpand = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <div className={style.about}>
      <h2>KROVATO -       !</h2>
      
      <div className={style.content}>
        <p>      :  , , ,    ,     .               ,   .         .       ,     .   ,</p>
        
        <div className={`${style.expandableContent} ${isExpanded ? style.expanded : ''}`}>
          <p>    ,    ,         - .           ,        .   ,  ,   ,    ,      ,    .        :           .            ,          . ..</p>
          
          <p>      ,              .  ,        ,               .          ,    ,      '.         ,       ,      .           ,            .       !</p>
        </div>
        
        <div className={`${style.fadeOverlay} ${isExpanded ? style.hidden : ''}`}></div>
      </div>
      
      <div className={style.bedButtonContainer} onClick={toggleExpand}>
        <div className={`${style.bedButton} ${isExpanded ? style.bedOpen : ''}`}>
          <div className={style.bedHeadboard}></div>
          <div className={style.bedFrame}></div>
          <div className={style.bedMattress}></div>
          <div className={style.bedPillow}></div>
          <div className={style.bedBlanket}></div>
        </div>
      </div>
    </div>
  );
};
