import { SharesItem } from "./Item/Item";

import style from "../Shares.module.scss";

export const SharesList = () => {
  const sharesList = [
    {
      id: "sharing-1",
      text: "      7000 . ( )      *",
    },
    {
      id: "sharing-2",
      text: "      10000 .  5%   + ,      *",
    },
    {
      id: "sharing-3",
      text: "   7000 .     *",
    },
    {
      id: "sharing-4",
      text: "   10000 .     * +  5%  ",
    },
    {
      id: "sharing-5",
      text: "    2-      *",
    },
  ];

  return (
    <ul className={style.shares__list}>
      {sharesList.map((item) => (
        <SharesItem key={item.id} svg={item.svg} text={item.text} />
      ))}
    </ul>
  );
};
